/* General styles */
body {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
    color: #333;
  }
  
  .nav-bar {
    background-color: #fff;
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
  }
  
  .nav-bar nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
  }
  
  .nav-bar h1 {
    font-size: 1.5em;
    margin: 0;
    color: #333;
  }
  
  .nav-bar span {
    font-size: 1em;
    margin: 0;
    color: #333;
  }
  
  /* Checkout container styles */
  .checkout-container {
    display: flex;
    gap: 20px;
    padding: 20px;
  }
  
  /* Left section styles */
  .left-section {
    width: 60%;
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
  }
  
  /* Checkout section styles */
  .checkout-section {
    margin-bottom: 20px;
  }
  
  .checkout-section h1,
  .checkout-section h3 {
    font-size: 1.2em;
    margin: 0 0 10px 0;
  }
  
  .checkout-section input[type="text"],
  .checkout-section select,
  .checkout-section textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 3px;
    box-sizing: border-box;
    margin-bottom: 10px;
  }
  
  .checkout-section .error {
    color: #f00;
    margin-bottom: 5px;
  }
  
  .checkbox-section {
    display: flex;
    align-items: center;
  }
  
  .checkbox-section input[type="checkbox"] {
    margin-right: 5px;
  }
  
  .checkbox-section label {
    margin: 0;
  }
  
  /* Shipping section styles */
  .shipping-section {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
  }
  
  .shipping {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .shipping p {
    margin: 0;
    font-weight: bold;
  }
  
  .shipping span {
    margin: 0;
  }
  
  /* Payment section styles */
  .pay-section {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
  }
  
  .payment-card {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 5px;
  }
  
  .payment-card-header {
    margin-bottom: 20px;
  }
  
  .payment-card-header h2 {
    margin: 0 0 5px 0;
  }
  
  .payment-card-header p {
    margin: 0;
    color: #666;
  }
  
  .payment-card-body {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .payment-card-option {
    display: flex;
    align-items: center;
  }
  
  .payment-card-option input[type="radio"] {
    margin-right: 5px;
  }
  
  .payment-card-option label {
    margin: 0;
  }
  
  .payment-card-details {
    margin-top: 20px;
  }
  
  /* Submit button styles */
  .checkout__submit {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .checkout__submit:hover {
    background-color: #0056b3;
  }
  
  /* Right section styles */
  .right-section {
    width: 40%;
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
  }
  
  .checkout__order {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .checkout__order-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
  }
  
  .checkout__order-item img {
    width: 50px;
    height: 50px;
    border-radius: 5px;
  }
  
  .checkout__order-item-info {
    margin-left: 10px;
  }
  
  .checkout__order-item-info h4 {
    margin: 0 0 5px 0;
  }
  
  .checkout__order-item-info p {
    margin: 0;
    color: #666;
  }
  
  .checkout__order-item span {
    margin-left: auto;
    font-weight: bold;
  }
  
  .checkout__order-summary,
  .checkout__order-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .checkout__order-summary p {
    margin: 0;
  }
  
  .checkout__order-summary span {
    font-weight: bold;
  }
  
  .checkout__order-total p {
    margin: 0;
    font-size: 1.2em;
    font-weight: bold;
  }
  
  .checkout__order-total span {
    font-size: 1.2em;
    font-weight: bold;
  }
  
  /* Toast styles */
  .Toastify__toast-container {
    position: fixed;
    bottom: 20px;
    right: 20px;
  }
  
  .Toastify__toast {
    background-color: #fff;
    color: #333;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }